import React,{useState, useEffect} from 'react'

import useFetchDiaries  from '../custmon-hooks/useFetchDiaries'
import useFetchDiaryContent from '../custmon-hooks/useFetchDiaryContent'

import './diary-list.css'
import './column-head.css'

import Calendar from 'react-github-contribution-calendar'
import { Accordion, Icon, Rating } from 'semantic-ui-react'

import paragraph from '../../assets/paragraph.png'

var until = new Date().toLocaleDateString()
.replace(/\//g,'-')

DiaryList.displayName = 'diary-list'

export default function DiaryList(){
  const [activeIndex, setActiveIndex] = useState(null)

  const values_ = useFetchDiaries() // 每次组件更新都会执行
  const diaryContent = useFetchDiaryContent(activeIndex) // 每次组件更新都会执行

  let values = {}
  console.log(diaryContent)
  for(let diaryItem of values_){
    values[diaryItem.date] = diaryItem.rate
  }
  //console.log(values)
  function handleClick(e, titleProps){
    const { index } = titleProps
    const newIndex = activeIndex === index ? -1 : index

    setActiveIndex(newIndex) // 每次调用都会驱动组件更新
  }
  return(
    <div>
      <h4 className='column-head'
          id='diary-list-head'
      >技术日记</h4>
      <div id='diary-calendar'>
        <Calendar 
          values={values} 
          until={until} 
          />
      </div>
      {
        values_.map(diary=>
          <Accordion key={diary.date}>
            <Accordion.Title
              active={activeIndex === diary.date}
              index={diary.date}
              onClick={handleClick}
            >
              <Icon name='dropdown' />
              {diary.date}
              <Rating 
                icon='star' 
                size='mini'
                disabled
                maxRating={5} 
                rating={diary.rate} 
              />
            </Accordion.Title>
            <Accordion.Content active={activeIndex === diary.date}>
              <p>
               {
               diaryContent?
               diaryContent.content
               :
               <img src={paragraph} 
                    alt=''
                    style={{width: '100px'}}
              />
              }
              </p>
            </Accordion.Content>
          </Accordion>
        )
      }
      
    </div>
  )
}