import React,{
  useState, 
  useEffect, 
  useLayoutEffect} from 'react'
import {withRouter} from 'react-router-dom'
import {connect} from 'react-redux'

import marked from 'marked'
import hljs from 'highlight.js'

import {
  Divider, 
  Label,
  Loader, 
  Image, 
  Dimmer, 
  Segment} from 'semantic-ui-react'
import 'semantic-ui-css/semantic.min.css'

// 无后台评论系统
import Valine from 'valine';

import {fetchArticleDetails} from './actions'

import Header from '../../components/common-components/header'

import {store} from '../../store/index'

import paragraph from '../../assets/paragraph.png'
import tag_react from '../../assets/React.png'
import tag_vue from '../../assets/Vue.png'
import tag_docker from '../../assets/Docker.png'
import tag_node from '../../assets/node.png'
import tag_js from '../../assets/javascript.png'
import No from '../../assets/NO.png'

import './style.css'
import './code-style.css'
import './content-style.css'

var mapStateToProps=({articleDetailsReducer})=>({
  articleDetailsObj: articleDetailsReducer
})

var mapDispatchToProps = (dispatch)=>({
  fetchArticle: title=>dispatch(fetchArticleDetails(title))
})

export default withRouter( 
  connect(mapStateToProps, mapDispatchToProps)(Article) 
)

const tags = {
  React: tag_react,
  Vue: tag_vue,
  Docker: tag_docker,
  Node: tag_node,
  javascript: tag_js
}

function Article(props){
  let { 
    history, 
    match, 
    location, 
    route, 
    dispatch, 
    articleDetailsObj } = props

  const [article_obj, setArticle_obj] = useState({})
  
  useEffect(() => {
    props.fetchArticle(match.params.articleTitle) 
  }, [])

  useEffect(
    ()=>{
      let { subscribe, getState } = store
      let unsub_listen_article_details = subscribe(()=>{
        let { articleDetailsReducer } = getState()
        let { articleObj }= articleDetailsReducer.fetchArticleDetails
        console.log('articleObj:', articleObj)
        
        let conteiner = document.getElementById('content')
        conteiner && ( conteiner.innerHTML = marked(articleObj.content, {
          highlight: function (code) {  //代码高亮
              return hljs.highlightAuto(code).value
          }
        }) )
        setArticle_obj(articleObj)
      })

      return ()=>{
        unsub_listen_article_details()
      }
    }
    ,[])

    useLayoutEffect(() => {
      
      new Valine({
        el:'#vcomments',
        appId: '8qaiegzP4E14qhu5VrOL8yhX-gzGzoHsz',
        appKey: 'tAbdPMGf21F1afgAAkB23LNX',
        placeholder: '尽情发炎吧',
        notify: true,
        avatar: 'mp',
        pageSize: 20,
        visitor: true,
        recordIP: true,
    })
    }, [])
  
  function matchTagImg(tag){
    return tags[tag]
  }

  return(
    <div className='article-details-page'>
      <Header/>
      <div id='details'>
        <div>
          <h2 id='title'>{article_obj.title}</h2>
          <div id='last-modified'>{article_obj.lastModified}</div>
          {
            article_obj.content ?
           null
            :
            <Segment>
              <Dimmer active inverted>
                <Loader inverted content='Loading' />
              </Dimmer>
              <Image src={paragraph} />
            </Segment>
          }
          <div id='content'></div>
          <Divider horizontal>标签</Divider>
          <div id='tags'>
            {
              article_obj.tags && [...article_obj.tags].map(tag=>
                <Label as='a' image key={tag}>
                  <img src={matchTagImg(tag) || No} alt=''/>
                  {tag}
                </Label>
              )
            }
          </div>
          <Divider horizontal>评论</Divider>
          <div id='vcomments'></div>
        </div>
      </div>
    </div>
  )
}