import React from 'react'

import './recent-act-list.css'
import './column-head.css'

import useFetchRctAct from '../custmon-hooks/useFetchRctAct'

import {Timeline, TimelineEvent} from 'react-event-timeline'

RecentActList.displayName = 'act-list'

export default function RecentActList(){
  let act = useFetchRctAct()
  //act = [...act]
  console.log('act:', act)
  return(
    <div>
      <h4 className='column-head'
          id='act-list-head'
      >
        最近活动
      </h4>
      <div id='act-timeline'>
        <Timeline>
        {
          act &&
          act.map(item=>
            <TimelineEvent title={item.title}
                           createdAt={item.date}
                           key={item.date}
            >
             {item.content}
            </TimelineEvent>
          )
        }
        </Timeline>
      </div>
    </div>
  )
}