import React,{useState, useEffect} from 'react'
import {connect} from  'react-redux'
import {withRouter}  from 'react-router-dom'
/**自定义组件 */
import ArticleList from '../../components/common-components/articles-list'
import DiaryList from '../../components/common-components/diary-list'
import RecentActList from '../../components/common-components/recent-act-list'
import Header from '../../components/common-components/header'
/** 样式 */
import './style.css'

import {store} from '../../store/index'
import { changeTab_on_mobile } from './actions'

function mapDispatchToProps(dispatch){
  return {
    changeTab: function(tabName){
      dispatch(changeTab_on_mobile(tabName) )
    }
  }
}

export default withRouter( connect(null, mapDispatchToProps)(Home))

function Home(props){
  //console.log(props)
  let {changeTab} = props
  const [tab_show, setTab_show] = useState('article-list')

  useEffect(() => {
    let { subscribe, getState } = store
    let unsub = subscribe(()=>{
      let { tabName }= getState().homeReducer.tabReducer
      //console.log({tabName})
      setTab_show(tabName)
    })
    return () => {
      unsub()
    }
  }, [])

  function renderColumns(components){
    return(
      components.map(Com=>
        <div key={Com.displayName}
             className={
               Com.displayName===tab_show ?
               'show-tab':'hide-tab'
            }
        >
          <Com/>
        </div>
      )
    )
  }
  function renderTabNavOnMobile(){
    const tabs = [
      { key:'article-list', name:'文章' },
      { key:'act-list', name:'最近活动' },
      { key:'diary-list', name:'技术日记' } ]

    return(
      <div className='mobile-tab'>
        {
          tabs.map(tab=>
            <div key={tab.key}
                 className='tab-nav-item'
                 id={
                   tab_show===tab.key ?
                   'tab-selected':null
                }
                 onClick={()=>changeTab(tab.key)}
            >
              {tab.name}
            </div>
          )
        }
      </div>
    )
  }
  return(
    <div className='home-page'>
      <Header/>
      { renderTabNavOnMobile() }
      <div className='columns-act-article-diary'>
        {renderColumns([RecentActList, ArticleList, DiaryList])}
      </div>
    </div>
  )
}
