import React,{useState, useEffect} from 'react'
import { Link } from 'react-router-dom'

import {store} from '../../store'

import { useFetchArticles } from '../custmon-hooks/useFetchArticles'
import ArticleFilter from './article-filter'
import LazyListHoc from './LazyList'

import { Label } from 'semantic-ui-react'
import 'semantic-ui-css/semantic.min.css'

import tag_react from '../../assets/React.png'
import tag_vue from '../../assets/Vue.png'
import tag_docker from '../../assets/Docker.png'
import tag_node from '../../assets/node.png'
import tag_js from '../../assets/javascript.png'
import tag_selfCreate from '../../assets/creator.png'
import tag_translate from '../../assets/translate.png'
import tag_python from '../../assets/python.png'
import No from '../../assets/NO.png'

import './articles-list.css'
import './column-head.css'

const tags = {
  React: tag_react,
  Vue: tag_vue,
  Docker: tag_docker,
  javascript: tag_js,
  Node: tag_node,
  Python: tag_python,
  原创: tag_selfCreate,
  翻译: tag_translate
}

ArticleList.displayName = 'article-list'

export default function ArticleList(){
  let {getState, subscribe} = store
  let filters_init = getState().commonReducers.articleFilterReducer
  const [article_tag, setArticle_tag] = useState([...filters_init])
  //const [artilces, setArtilces] = useState([])
  let articles = useFetchArticles(article_tag)
  
  useEffect(() => {
    let unsub_listene_filter = subscribe(()=>{
      let tags = getState().commonReducers.articleFilterReducer
      //console.log('store tags:', tags)
      setArticle_tag(tags)
    })

    return()=>{
      unsub_listene_filter()
    }
  }, [])

  function matchTagImg(tag){
    return tags[tag]
  }

  function articleListCom(props){
    let {list} = props
    console.log({list})
    let articles_arr = list
    return(
      articles_arr.map(
        article=>
        <div key={article.id} 
             className='article-item'
        >
          <div className='flex-box'>
            <div className="img-container">
              <img src={article.tumbnail} className='tumbnail' alt=""/>
            </div>
            <div className='title-date'>
              <Link to={'/article/'+article.title}>
                <h5>{article.title}</h5>
              </Link>
              <div className='last-modified'>{article.lastModified}</div>
            </div>
          </div>
          <div className='tags'>
            {
              article.tags.map(
                tag=>
                <div key={tag} className='tag-item'
                >
                  <Label as='a' 
                         image 
                         size='mini'>
                    <img src={matchTagImg(tag) || No} alt=''/>
                    {tag}
                  </Label>
                </div>
              )
            }
          </div>
        </div>
      )
    )
  }
 
  return (
    <div>
      <h4 className='column-head'
          id='article-list-head'
      >
      文章</h4>
      <ArticleFilter/>
      <div className='articles-list'>
        {
          LazyListHoc(articles)(articleListCom)
        }{/*
          articleListCom({list: articles})
        */}
      </div>
    </div>
  )
}
